import './ToolTip.scss';
import {useEffect, useState} from "react";
import cn from "classnames";
import {countries} from "../countries";

export default function ToolTip({newPosition, countryData}) {

    const [position, setPosition] = useState({
        cordsX: 510,
        cordsY: 200,
    });
    const [country, setCountry] = useState(countries[7]);

    useEffect(()=> {
        if (newPosition) {
            setPosition(newPosition)
        }
    }, [newPosition])

    useEffect(()=> {
        if (countryData) {
            setCountry(countryData)
        }
    }, [countryData])

    return (
        <div
            style={{top: `${position.cordsY}px`, left: `${position.cordsX}px`}}
            className={cn('map__tooltip', {'map__tooltip_moved': newPosition})}
        >
            <div className="map__tooltip-inner">
                {country.flag}
                <span>{country.name}</span>
            </div>

        </div>
    )
}