import './FourBoxes.scss';
import cn from "classnames";

export default function FourBoxes({boxes, upper}) {

    const boxesList = boxes.map(({title, text}) => {
        return (
            <div className={cn('four-boxes__box', {'four-boxes__box_upper': upper})} key={title}>
                <h3>{title}</h3>
                <div>{text}</div>
            </div>
        );
    });

    return (
        <div className={'four-boxes flex'}>
            {boxesList}
        </div>
    );
}