import './CustomerButton.scss';
import cn from "classnames";
import {Link} from "react-router-dom";

export default function CustomerButton({buttonName, className, onClick, to, icon}) {

    return (
        <>
            {onClick ? (
                <div onClick={() => onClick()} className={cn("customer-button", className)}>
                    {buttonName}
                    {icon ? icon : <img src="/images/rightArrow.png" alt="Right arrow"/>}
                </div>
            ) : (
                <Link to={to} className={cn("customer-button", className)}>
                    {buttonName}
                    {icon ? icon : <img src="/images/rightArrow.png" alt="Right arrow"/>}
                </Link>
            )}
        </>
    );
}