import './MediaTabs.scss';
import {Link, useLocation} from "react-router-dom";
import cn from "classnames";
import {useMediaTabs} from "../../../../hooks/useMediaTabs";

export default function MediaTabs() {

    const {pathname} = useLocation();
    const {tabs} = useMediaTabs();

    return (
        <div className={'media-tabs'}>
            <div className="media-tabs__tabs-wrap">

                {tabs.map(tab => {
                    return (
                        <Link
                            to={tab.url}
                            key={tab.name}
                            className={cn('media-tabs__tab media-tab', {'media-tab_active': tab.url === pathname})}
                        >
                            <div className="media-tab__inner">
                                {tab.name}
                            </div>
                        </Link>
                    )
                })}

            </div>
        </div>
    );
}