import './ChooseCard.scss';
import {useEffect, useRef, useState} from "react";

export default function ChooseCard({card, onClick, isOpened}) {

    const contentRef = useRef(null);
    const [contentHeight, setContentHeight] = useState(null);

    useEffect(() => {
        setContentHeight(contentRef.current?.getBoundingClientRect().height)
    }, []);

    return (
        <div className="choose-us__card choose-us-card">
            <div onClick={()=> onClick()} className="choose-us-card__head">
                <h3>{card.title}</h3>
                <div className="choose-us-card__image-wrap">
                    <img src={`/images/Blockchain/${card.icon}`}/>
                </div>
            </div>
            <div
                style={isOpened ? {height: `${contentHeight}px`} : {height: '0px'}}
                className="choose-us-card__content-wrap"
            >
                <div ref={contentRef} className="choose-us-card__paragraph">
                    {card.paragraph}
                </div>
            </div>
        </div>
    );
}