import './AccordionItem.scss';
import cn from "classnames";
import {ReactComponent as Arrow} from "../../../../icons/arrow_up-right.svg";
import {useEffect, useRef, useState} from "react";
import ItemWithIcon from "../../../../components/ItemWithIcon/ItemWithIcon";

export default function AccordionItem({item, onClick, itemIsOpened}) {

    const contentRef = useRef(null);
    const [contentHeight, setContentHeight] = useState(null);

    useEffect(() => {
        setContentHeight(contentRef.current?.getBoundingClientRect().height)
    }, []);

    const innerList = item.list?.map(elem => {
        return (
            <ItemWithIcon item={elem} key={elem.desc + elem.icon}/>
        );
    })

    return (
        <div className={'accordion__item accordion-item'}>
            <div className="accordion-item__header" onClick={() => onClick()}>
                <div
                    className={cn("accordion-item__header-inner flex", {'accordion-item__header-inner_opened': itemIsOpened})}>
                    <h2>{item.title}</h2>
                    <div className="invisible"/>
                    <div>
                        <Arrow/>
                    </div>
                </div>
            </div>
            <div
                style={itemIsOpened ? {height: `${contentHeight}px`} : {height: '0px'}}
                className={cn("accordion-item__content-wrap", {'accordion-item__content-wrap_opened': itemIsOpened})}
            >
                <div
                    ref={contentRef}
                    className="accordion-item__content-inner accordion-content flex"
                >
                    <div className="accordion-content__left">
                        {item.text}
                    </div>

                    {item.name && (
                        <div className="accordion-content__right">
                            <h3>Our marketing analysis package includes</h3>
                            <ul>
                                {innerList}
                            </ul>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}