import {useLocation} from "react-router-dom";
import News from "../pages/MediaPage/Media/MediaContent/News/News";
import DevBlog from "../pages/MediaPage/Media/MediaContent/DevBlog/DevBlog";

export function useMediaTabs() {

    const {pathname} = useLocation();

    const tabs = [
        {
            url: '/media/devblog',
            name: 'devblog',
            component: <DevBlog/>
        },
        {
            url: '/media/news',
            name: 'news',
            component: <News/>
        },
    ]

    return {pathname, tabs};
}