import './AboutBlock.scss';
import SectionHeader from "../../SectionHeader/SectionHeader";
import cn from "classnames";
import CustomerButton from "../../CustomerButton/CustomerButton";
import {useSetRecoilState} from "recoil";
import {isModalOpenState} from "../../layout/Modal/isModalOpenState";

export default function AboutBlock({mainTitle, boldTitle, buttonName, cards, revers}) {

    const setModalIsOpen = useSetRecoilState(isModalOpenState);

    const title = boldTitle ? (
        <h2 className={'semi-title'}>{boldTitle} <span>{mainTitle}</span></h2>
    ) : (
        <SectionHeader title={mainTitle}/>
    );

    const image = cards[0].image ? (
        <img src={cards[0].image} alt="Background"/>
    ) : (
        <>
            <h3>{cards[0].title}</h3>
            <p>{cards[0].text}</p>
        </>
    );

    return (
        <section className={'about-block'}>
            <div className="container">
                {title}
                <div
                    className={cn("about-block__content-wrap flex", {
                        "about-block__content-wrap_revers": revers
                    })}
                >
                    <div className="about-block__left">
                        {image}
                    </div>
                    <div className="about-block__right">
                        <h3>{cards[1].title}</h3>
                        <p>{cards[1].text}</p>

                        {buttonName && <CustomerButton buttonName={buttonName} onClick={() => setModalIsOpen(true)}/>}

                    </div>
                </div>
            </div>
        </section>
    );
}