import './Form.scss';
import {ReactComponent as FileAdd} from "../../../../icons/fileAdd.svg";
import {useState} from "react";

const initialFormInputs = {
    email: '',
    phone: '',
    name: '',
    textarea: ''
}

export default function Form({footer}) {

    const [theFile, setTheFile] = useState(null);
    const [selectedFile, setSelectedFile] = useState();

    const [inputs, setInputs] = useState(initialFormInputs);
    // const [checkbox, setCheckbox] = useState(false);

    // const handler = useCallback(() => {
    //     setCheckbox(!checkbox);
    //     console.log('checkbox', checkbox)
    // }, [checkbox]);

    const handleInputChange = (e) => {
        const name = e.target.name;
        const value = e.target.value;

        if (name === 'phone') {
            const telReg = /^(\d|[\s_():-=+-]){0,17}$/;

            if (telReg.test(value)) {
                setInputs(values => ({...values, [name]: value}))
            } else {
                return false
            }
        } else if (name === 'email' && value.length < 40) {
            setInputs(values => ({...values, [name]: value}))

        } else if (name === 'name' && value.length < 30) {
            setInputs(values => ({...values, [name]: value}))

        } else if (name === 'textarea' && value.length < 1000) {
            setInputs(values => ({...values, [name]: value}))
        }
    }

    const onImageChange = (e) => {
        try {
            const file = e.target.files[0];

            setTheFile(file);
            setSelectedFile(URL.createObjectURL(file));
        } catch (e) {
            console.log('error', e)
        }
    };

    const sendMail = async (e) => {
        e.preventDefault();
        const formData = new FormData(e.target)

        await fetch('https://nft.raritygram.io/sendform', {
            method: 'POST',
            body: formData
        })
            .then((response) => response.json())
            .then((data) => {
                console.log('Success:', data);
                setInputs(initialFormInputs)
            })
            .catch((error) => {
                console.error('Error:', error);
            });
    }

    return (
        <form onSubmit={sendMail} method={'POST'} className={'form'}>
            <div className="form__form-group">
                <input
                    required={true}
                    type="email"
                    placeholder="email@mail.com *"
                    name={'email'}
                    value={inputs.email}
                    onChange={handleInputChange}
                />
            </div>
            <div className="form__form-group">
                <input
                    required={true}
                    type="tel"
                    className={'form__tel-input'}
                    placeholder="+1 (234) 567 890 *"
                    name={'phone'}
                    value={inputs.phone}
                    onChange={handleInputChange}
                />
            </div>
            <div className="form__form-group">
                <input
                    required={true}
                    type="text"
                    placeholder="Your name *"
                    name={'name'}
                    value={inputs.name}
                    onChange={handleInputChange}
                />
            </div>
            <div className="form__form-group">
                <textarea
                    placeholder="Briefly describe your project or attach a file/link"
                    name={'textarea'}
                    value={inputs.textarea}
                    onChange={handleInputChange}
                />
            </div>

            <div style={footer ? {marginBottom: '20px'} : {marginBottom: '0px'}} className="form__form-group">

                <button className={'customer-button send-button'} type={"submit"}>
                    Send
                    <img src="/images/rightArrow.png" alt="Right arrow"/>
                </button>

                {selectedFile && <img className={'form-img-preview'} src={selectedFile} alt="file preview"/>}

                <div>
                    <label className="form__label-file">
                        <FileAdd/>
                        attach a file
                        <input
                            accept="image/*"
                            type="file"
                            onChange={onImageChange}
                            name="file[]"
                            multiple
                        />
                    </label>
                </div>
            </div>
        </form>
    );
}