import './ContactUs.scss';
import BorderButton from "../../../components/BorderButton/BorderButton";
import {useSetRecoilState} from "recoil";
import {isModalOpenState} from "../../../components/layout/Modal/isModalOpenState";

export default function ContactUs({cards}) {

    const setModalIsOpen = useSetRecoilState(isModalOpenState);

    return (
        <section className="contact-us">
            <div className="container flex">
                <div className="contact-us__contact">
                    <h3>
                        {cards ? cards[0].title : 'Contact us now'}
                    </h3>
                    <p>
                        {cards ? cards[0].text : 'We provide a free consultation on any of our services, feel free to email us. You can email us, leave a request in the form below or write to us on any social network. Happy to chat with you!'}
                    </p>
                    <BorderButton buttonName={'Contact us'} onClick={()=> setModalIsOpen(true)}/>
                </div>
                <div className="contact-us__additional">
                    <h3>
                        {cards ? cards[1].title : 'Additional services'}
                    </h3>
                    <p>
                        {cards ? cards[1].text : 'ITWorkin also provides projects with support services. When working with us, you can be 100% sure that your project will scale. Our team will provide support for your business, including all the services needed to help you take off.'}
                    </p>
                </div>
            </div>
        </section>
    );
}