import './CellHead.scss';
import cn from "classnames";
import {useRecoilState, useSetRecoilState} from "recoil";
import {isModalOpenState} from "../../../../components/layout/Modal/isModalOpenState";

export default function CellHead({title, price, dark}) {

    const setModalIsOpen = useSetRecoilState(isModalOpenState);

    return (
        <div style={dark ? {background: '#0D0D0D'} : {background: '#FAFAFA'}}
             className={cn('cell-head flex', {'cell-head_dark': dark})}>
            <h3>{title}</h3>
            <div className={'cell-head__price'}>{price}</div>
            <div className={'cell-head__button'} onClick={() => setModalIsOpen(true)}>Get a free consultation</div>
        </div>
    );
}