import './IdoSteps.scss';
import SectionHeader from "../../../components/SectionHeader/SectionHeader";
import CardWithImg from "../../../components/CardWithImg/CardWithImg";
import {useRef} from "react";
import {idoStepsCards} from "./idoStepsCards";
import Slider from 'react-slick';
import {settings} from "../../../components/Slider/sliderSetings";

export default function IdoSteps() {

    const SliderRef = useRef(null);
    const cards = idoStepsCards.map(card => {
        return (
            <CardWithImg card={card} key={card.title}/>
        );
    })

    return (
        <section className={'ido-steps'}>
            <div className="container">
                <SectionHeader
                    title={'ITworkin steps of IDO realization'}
                />
                <div className="ido-steps__grid">
                    {cards}
                </div>
                <div className="default__mobile-slider-wrap">
                    <Slider ref={SliderRef} {...settings}>
                        {cards}
                    </Slider>
                </div>
            </div>
        </section>
    );
}