import './DevBlogCard.scss';
import Date from "../../../../../../components/Date/Date";
import Author from "../../../../../../components/Author/Author";
import {Link} from "react-router-dom";

export default function DevBlogCard({card}) {

    return (
        <Link to={`/devblog/${card.name}`} className={'media-dev-card flex'}>
            <div className="media-dev-card__content">
                <Date date={card.date} />
                <div className="media-dev-card__content_inner">
                    <h3>{card.title}</h3>
                    <p>{card.desc}</p>
                </div>
                <Author author={card.author} dark={true}/>
            </div>
            <div className="media-dev-card__image-wrap">
                <img src={card.previewImg} alt="Devblog preview"/>
            </div>
        </Link>
    )
}