import './DevBlog.scss';
import DevBlogCard from "./DevBlogCard/DevBlogCard";
import {devblogs} from "../../../../../content/devblogs";

export default function DevBlog() {

    const devblogList = devblogs.map(item => {
        return (
            <DevBlogCard
                key={item.name}
                card={item}
            />
        )
    })

    return (
        <div className={'media-devblog'}>
            {devblogList}
        </div>
    )
}