import './ServicesCrypto.scss';
import SectionHeader from "../../../components/SectionHeader/SectionHeader";
import ChooseCard from "../../BlockchainPage/ChooseUs/ChooseCard/ChooseCard";
import {servicesCryptoCards} from "./servicesCryptoCards";
import {useState} from "react";
import {useIsMobile} from "../../../hooks/useIsMobile";

export default function ServicesCrypto() {

    const [openedItemIndex, setOpenedItemIndex] = useState(0);
    const isMobile = useIsMobile();

    return (
        <section className="services-crypto">
            <div className="container">
                <SectionHeader title={'Services'}/>
                <div className="services-crypto__grid">

                    {servicesCryptoCards.map((card, index) => {
                        return (
                            <ChooseCard
                                card={card}
                                key={card.title}
                                onClick={() => setOpenedItemIndex(index)}
                                isOpened={isMobile ? index === openedItemIndex : true}
                            />
                        );
                    })}

                </div>
            </div>
        </section>
    );
}