import './JoinForm.scss';
import BorderButton from "../BorderButton/BorderButton";
import cn from "classnames";

export default function JoinForm({className}) {

    const emailUsed = false;

    return (
        <form className={cn("join-form", className)}>
            <div className="form-group">
                <input
                type="text"
                placeholder="EMAIL@MAIL.COM *"
                />
                {emailUsed && <span className="email--error">Этот email уже был использован</span>}

                {/*<span className="email--error" v-if="emailUsed">Этот email уже был использован</span>*/}
            </div>

            <BorderButton buttonName="JOIN" className="join-form__button"/>
        </form>
    );
}