import './App.scss';
import {BrowserRouter, Route, Routes, useLocation} from 'react-router-dom'
import MainPage from "./pages/MainPage/MainPage";
import Header from "./components/layout/Header/Header";
import {useRecoilState, useRecoilValue} from "recoil";
import Footer from "./components/layout/Footer/Footer";
import BlockchainPage from "./pages/BlockchainPage/BlockchainPage";
import NftDevPage from "./pages/NftDevPage/NftDevPage";
import FullServicePage from "./pages/FullServicePage/FullServicePage";
import SmartContractPage from "./pages/SmartContractPage/SmartContractPage";
import CryptoSupport from "./pages/CryptoSupport/CryptoSupport";
import IdoLaunchPage from "./pages/IdoLaunchPage/IdoLaunchPage";
import Modal from "./components/layout/Modal/Modal";
import ModalForm from "./components/ModalForm/ModalForm";
import {isModalOpenState} from "./components/layout/Modal/isModalOpenState";
import ScrollToTop from "./scrollToTop";
import DaoDev from "./pages/inner/DaoDev/DaoDev";
import DefiDev from "./pages/inner/DefiDev/DefiDev";
import DappDev from "./pages/inner/DappDev/DappDev";
import GamefiDev from "./pages/inner/GamefiDev/GamefiDev";
import CryptoWalletDev from "./pages/inner/CryptoWalletDev/CryptoWalletDev";
import Exchange from "./pages/inner/Exchange/Exchange";
import SemiFungibleToken from "./pages/inner/SemiFungibleToken/SemiFungibleToken";
import SmartDev from "./pages/inner/SmartDev/SmartDev";
import DigitalAssets from "./pages/inner/DigitalAssets/DigitalAssets";
import Tokenization from "./pages/inner/Tokenization/Tokenization";
import NftMarketing from "./pages/inner/NftMarketing/NftMarketing";
import IcoMarketing from "./pages/inner/IcoMarketing/IcoMarketing";
import IeoMarketing from "./pages/inner/IeoMarketing/IeoMarketing";
import StoMarketing from "./pages/inner/StoMarketing/StoMarketing";
import CryptoInfluencers from "./pages/inner/CryptoInfluencers/CryptoInfluencers";
import CryptoCommunity from "./pages/inner/CryptoCommunity/CryptoCommunity";
import MediaPage from "./pages/MediaPage/MediaPage";
import ArticlePage from "./pages/ArticlePage/ArticlePage";
import ContactUsPage from "./pages/ContactUsPage/ContactUsPage";
import {useEffect, useRef} from "react";
import Lottie from "lottie-web";
import animationSpinner from '../src/icons/preLoaderRotate.json'
import cn from "classnames";
import {isLoaderState} from "./isLoaderState";
import ProjectsPage from "./pages/ProjectsPage/ProjectsPage";

function App() {

    const spinnerAnimationRef = useRef();
    const [modalIsOpen, setModalIsOpen] = useRecoilState(isModalOpenState);
    const isLoading = useRecoilValue(isLoaderState)

    useEffect(() => {
        Lottie.loadAnimation({
            container: spinnerAnimationRef.current,
            animationData: animationSpinner,
            renderer: 'svg',
        });
    }, []);

    return (
        <BrowserRouter>
            <ScrollToTop/>
            <Modal isOpened={modalIsOpen} onClose={() => setModalIsOpen(false)}>
                <ModalForm/>
            </Modal>
            <Header/>
            <div className={cn('loader-overlay', {'loader-overlay_active': isLoading})}>
                <div ref={spinnerAnimationRef}/>
            </div>
            <Routes>
                <Route exact path="/" element={<MainPage/>}/>
                <Route path="/media/:name" element={<MediaPage/>}/>
                <Route path="/devblog/:name" element={<ArticlePage/>}/>

                <Route path="/blockchain" element={<BlockchainPage/>}/>
                <Route path="/blockchain/dao-development" element={<DaoDev/>}/>
                <Route path="/blockchain/defi-development" element={<DefiDev/>}/>
                <Route path="/blockchain/dapp-development" element={<DappDev/>}/>
                <Route path="/blockchain/gamefi-development" element={<GamefiDev/>}/>
                <Route path="/blockchain/crypto-wallet-development" element={<CryptoWalletDev/>}/>
                <Route path="/blockchain/p2p-exchange-development" element={<Exchange/>}/>
                <Route path="/blockchain/smart-contract-development" element={<SmartDev/>}/>

                <Route path="/nft-development" element={<NftDevPage/>}/>
                <Route path="/nft-development/semi-fungible-token" element={<SemiFungibleToken/>}/>
                <Route path="/nft-development/digital-asset" element={<DigitalAssets/>}/>
                <Route path="/nft-development/tokenization" element={<Tokenization/>}/>
                <Route path="/nft-development/nft-marketing" element={<NftMarketing/>}/>

                <Route path="/crypto-marketing" element={<FullServicePage/>}/>
                <Route path="/crypto-marketing/ico-marketing" element={<IcoMarketing/>}/>
                <Route path="/crypto-marketing/ieo-marketing" element={<IeoMarketing/>}/>
                <Route path="/crypto-marketing/sto-marketing" element={<StoMarketing/>}/>
                <Route path="/crypto-marketing/crypto-influencers" element={<CryptoInfluencers/>}/>
                <Route path="/crypto-marketing/crypto-community" element={<CryptoCommunity/>}/>

                <Route path="/smart-contract" element={<SmartContractPage/>}/>
                <Route path="/crypto-support" element={<CryptoSupport/>}/>
                <Route path="/ido-launch" element={<IdoLaunchPage/>}/>
                <Route path="/contact-us" element={<ContactUsPage/>}/>
                <Route path="/projects" element={<ProjectsPage/>}/>
            </Routes>
            <Footer/>
        </BrowserRouter>
    );
}

export default App;