import './Covered.scss';
import JoinForm from "../JoinForm/JoinForm";
import {useSetRecoilState} from "recoil";
import {isModalOpenState} from "../layout/Modal/isModalOpenState";
import BorderButton from "../BorderButton/BorderButton";

export default function Covered({title, text, marginBottom, customButton}) {

    const setModalIsOpen = useSetRecoilState(isModalOpenState);

    return (
        <section style={marginBottom ? {marginBottom: `${marginBottom}px`} : {marginBottom: '0px'}} className="covered">
            <div className="container">
                <div className="covered__img-wrap">
                    <img src="/images/Blockchain/checklist.svg" alt="Checklist"/>
                </div>
                <div className="covered__middle-line"/>
                <div className="covered__content">
                    <h2>{title}</h2>

                    {text && <p style={customButton ? {marginBottom: '0px'} : {marginBottom: '32px'}}>{text}</p>}

                    {customButton ? (
                        <BorderButton
                            buttonName={'Contact us'}
                            className={'covered__button'}
                            onClick={() => setModalIsOpen(true)}
                        />
                    ) : (
                        <JoinForm className="covered__form"/>
                    )}

                </div>
            </div>
        </section>
    );
}