import './Service.scss';
import {Link} from "react-router-dom";
import {ReactComponent as ArrowUp} from "../../../../icons/arrow_up-right.svg";

export default function Service({service}) {
    return (
        <li className="services-list__card service container">
            <Link to={`/${service.url}`}>
                <div className="service-top__wrap">
                    <h3 className="service__title">
                        {service.title}
                    </h3>
                    <div className="service__link">
                        <ArrowUp/>
                    </div>
                </div>
                <div className="service-informations">
                    <div className="service-informations__wrap">
                        <span className="service-informations__text">
                            {service.text}
                        </span>
                        <ul className="service-informations__tag-wrap flex">
                            {service.tags.map(tag => {
                                return (
                                    <li key={tag} className="service-informations__tag">
                                        {tag}
                                    </li>
                                );
                            })}
                        </ul>
                    </div>
                    <div className="service__img">
                        <img src={`/images/Services/${service.img}`} alt="Service" className="service__img--mb"/>
                    </div>
                </div>
            </Link>
        </li>
    );
}