import './News.scss';
import MediaNewsItem from "./MediaNewsItem/MediaNewsItem";
import {news} from "../../../../../content/news";

export default function News() {

    const newsList = news.map(item => {
        return <MediaNewsItem key={item.title} item={item}/>;
    })

    return (
        <div className={'media-news'}>

            <ul className={'media-news__list'}>
                {newsList}
            </ul>
        </div>
    )
}