import './ChooseUs.scss';
import ChooseCard from "./ChooseCard/ChooseCard";
import {chooseCards} from "../../../data/chooseCards";
import {useState} from "react";
import {useIsMobile} from "../../../hooks/useIsMobile";

export default function ChooseUs() {

    const [openedItemIndex, setOpenedItemIndex] = useState(0);
    const isMobile = useIsMobile();

    return (
        <section className="choose-us">
            <div className="container">
                <h2>Why should you choose us?</h2>
                <div className="choose-us__grid">

                    {chooseCards.map((card, index) => {
                        return (
                            <ChooseCard
                                card={card}
                                key={card.title}
                                onClick={() => setOpenedItemIndex(index)}
                                isOpened={isMobile ? index === openedItemIndex : true}
                            />
                        );
                    })}

                </div>
            </div>
        </section>
    );
}