import './CardWithImg.scss';
import {Link} from "react-router-dom";
import cn from "classnames";

export default function CardWithImg({card}) {
    return (
        <div className={cn('card-with-img flex', {'card-with-img_dark': card.dark})}>
            <div>
                <h3>{card.title}</h3>
                <p>{card.paragraph}</p>
            </div>
            {card.buttonName && (
                <Link to={card.buttonLink} className={'card-with-img__button flex'}>
                    {card.buttonName}
                    <img src="/images/rightArrow.png" alt="Right arrow"/>
                </Link>
            )}
            <img src={card.image} alt="Card icon"/>
        </div>
    );
}