import './Partners.scss';
import {partnerList} from "./partnersList";


export default function Partners() {

    const partnersList = partnerList.map(item => {
        return (
            <a key={item.name} href={item.link} className="partners__list-item">
                <img
                    className="partners--not-hover"
                    src={item.imageFirst}
                    alt={item.name}
                />
                <img
                    className="partners--hover"
                    src={item.imageHover}
                    alt={item.name}
                />
            </a>
        )
    });

    return (
        <section className="partners">
            <h2>Our partners</h2>
            <div className="partners__list container">
                {partnersList}
            </div>
        </section>
    );
}