import './Accordion.scss';
import {accordionCards} from "./accordionCards";
import {useState} from "react";
import AccordionItem from "./AccordionItem/AccordionItem";

export default function Accordion() {

    const [itemIsOpenedIndex, setItemIsOpenedIndex] = useState(0);

    const onHeaderClick = (index) => {
        if (index === itemIsOpenedIndex) {
            setItemIsOpenedIndex(null)
        } else {
            setItemIsOpenedIndex(index)
        }
    }

    const accordionItems = accordionCards.map((item, index) => {
        return (
            <AccordionItem
                key={item.title}
                item={item}
                onClick={() => onHeaderClick(index)}
                itemIsOpened={index === itemIsOpenedIndex}
            />
        );
    })

    return (
        <section className="accordion">
            <div className="container flex">
                <div className="accordion__list flex">
                    {accordionItems}
                </div>
            </div>
        </section>
    );
}