import './ProjectsPage.scss';
import FirstScreen from "../MainPage/FirstScreen/FirstScreen";
import MetaPlatform from "../MainPage/MetaPlatform/MetaPlatform";
import NftRarity from "./NftRarity/NftRarity";
import ProjectSection from "./ProjectSection/ProjectSection";
import {ourProjectsList, rarityProjects} from "./rarityProjects";

export default function ProjectsPage() {

    const raritySections = rarityProjects.map((section, index) => {
        return (
            <NftRarity
                key={section.title}
                reverse={section.reverse}
                lastItem={index === rarityProjects.length - 1}
                title={section.title}
                text={section.text}
                buttonName={section.buttonName}
                buttonLink={section.buttonLink}
                media={section.media}
            />
        )
    });

    const ourProjects = ourProjectsList.map((project, index) => {
        return (
            <ProjectSection
                key={project.logo}
                item={project}
                firstSection={index === 0}
                presidents={index === 0}
            />
        )
    });

    return (
        <main className={'projects-page'}>
            <FirstScreen title={'OUR CASES'}/>
            <MetaPlatform projectsPage={true}/>

            {raritySections}

            {ourProjects}
        </main>
    )
}