import './AllSteps.scss';
import BorderButton from "../../../components/BorderButton/BorderButton";
import {ReactComponent as ArrowRight} from "../../../icons/Arrow-black-right.svg";
import {useSetRecoilState} from "recoil";
import {isModalOpenState} from "../../../components/layout/Modal/isModalOpenState";

export default function AllSteps({buttonName, content}) {

    const setModalIsOpen = useSetRecoilState(isModalOpenState);

    return (
        <section className={'all-steps'}>
            <div className="container flex">
                <div className="all-steps__main-content">
                    <h2>{content[0].title}</h2>
                    <p>{content[0].text}</p>
                    {buttonName && (
                        <BorderButton
                            onClick={() => setModalIsOpen(true)}
                            buttonName={buttonName}
                            className={'all-steps__button'}
                        />
                    )}

                </div>
                <div className="all-steps__list flex">

                    {content[1].title && <h3>{content[1].title}</h3>}

                    <ul>
                        {content[1].list.map(item => {
                            return (
                                <li key={item}>
                                    <ArrowRight/>
                                    {item}
                                </li>
                            );
                        })}
                    </ul>
                </div>
            </div>
        </section>
    );
}