import './DefaultCard.scss';
import {Link} from "react-router-dom";
import cn from "classnames";

export default function DefaultCard({card, className}) {

    return (
        <div className={cn("default-card", className)}>
            <img src={`images/Blockchain/${card.icon}`}/>
            {card.link ? (
                <Link to={card.link} className="default-card__link">
                    <div className="default-card__inner-wrap">
                        <img src={`images/Blockchain/${card.icon}`}/>
                        <h3>{card.title}</h3>
                        <p>{card.paragraph}</p>
                    </div>
                    <div className="default-card__read-more">
                        <span>Read more</span> →
                    </div>
                </Link>
            ) : (
                <div className="default-card__link">
                    <div className="default-card__inner-wrap">
                        <img src={`images/Blockchain/${card.icon}`}/>
                        <h3>{card.title}</h3>
                        {card.paragraph && <p>{card.paragraph}</p>}
                    </div>
                </div>
            )}
        </div>
    );
}