import './ArticlePage.scss';
import {useParams} from "react-router-dom";
import {devblogs} from "../../content/devblogs";
import ArticleHeader from "./ArticleHeader/ArticleHeader";
import {ReactComponent as ShareIcon} from "../../icons/share-chain.svg";
import ArticleMainContent from "./ArticleMainContent/ArticleMainContent";
import {useState} from "react";
import cn from "classnames";

export default function ArticlePage() {

    const {name} = useParams();
    const article = devblogs.find(x => x.name === name);
    const [isTooltipOpen, setIsTooltipOpen] = useState(false);
    const [isWaiting, setIsWaiting] = useState(false);

    const onShareButton = () => {
        if (isWaiting) {
            return false;
        }
        setIsWaiting(true);
        setIsTooltipOpen(true);

        navigator.clipboard.writeText(window.location.href);

        setTimeout(() => {
            setIsTooltipOpen(false);
            setIsWaiting(false);
        }, 2000)
    }

    return (
        <main className={'article'}>
            <ArticleHeader article={article}/>
            <section className={'article__share article-share'}>
                <div className="container">
                    <div onClick={onShareButton} className="article-share__share-button flex">
                        <ShareIcon/>
                        Share

                        <div className={cn('article-share__tooltip', {'article-share__tooltip_visible': isTooltipOpen})}>
                            <div className={'article-share__tooltip_inner'}>Copied successfully</div>
                        </div>
                    </div>
                </div>
            </section>
            <section className={'article__main-content article-main-content'}>
                <div className="container">
                    <ArticleMainContent/>
                </div>
            </section>
        </main>
    );
}