import './ProjectSection.scss';
import {Link} from "react-router-dom";
import {ReactComponent as Arrow} from "../../../icons/Arrow_1.svg";
import {useEffect, useRef} from "react";
import cn from "classnames";

export default function ProjectSection({item, firstSection}) {

    const buttonRef = useRef();

    useEffect(() => {
        buttonRef.current?.style.setProperty('--button-background', `${item.buttonBackground}`);
        buttonRef.current?.style.setProperty('--button-color', `${item.buttonColor}`);
    }, [])

    const background = item.background ? (
        <img
            src={`/images/projectsPage/${item.background}`}
            alt="Background"
        />
    ) : (
        <div style={{background: '#000'}}/>
    );

    return (
        <section className={cn('project-section', {
            'project-section_reverse': item.reverse,
            'project-section_first': firstSection
        })}>
            {background}
            <div className="container">
                <div className="project-section__content">
                    <img src={`/images/projectsPage/${item.logo}`} alt="Project logo"/>
                    <p>{item.text}</p>
                    <a ref={buttonRef} className={'project-section__button'} href={item.buttonLink}>
                        {item.buttonName}
                        <Arrow/>
                    </a>
                </div>
                <div style={{alignItems: item.imagePosition || 'center'}}
                     className={cn("project-section__image-wrap", {'project-section__image-wrap_presidents': firstSection})}>
                    <img src={`/images/projectsPage/${item.image}`} alt="Our project"/>
                </div>
            </div>
        </section>
    )
}