import './FirstScreenView.scss';
import CustomerButton from "../../../components/CustomerButton/CustomerButton";
import Socials from "../../../components/Socials/Socials";
import {useSetRecoilState} from "recoil";
import {isModalOpenState} from "../../../components/layout/Modal/isModalOpenState";

export default function FirstScreenView({image, title}) {

    const setModalIsOpen = useSetRecoilState(isModalOpenState);

    return (
        <section className={'first-screen-view'}>
            <div className="banner-wrap">
                <div className="container banner">

                    <div className="left">
                        <Socials className={'left__desktop-social'}/>
                        <div className="description">
                            <span className="description-text">
                                {title}
                            </span>
                            <CustomerButton buttonName="Become a customer" onClick={() => setModalIsOpen(true)}/>
                        </div>
                    </div>

                    <div className="right">
                        <Socials className={'right__mobile-socials'}/>
                        <div className="banner__img">
                            <img src={`images/${image}`} alt="Etherium"/>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
}