import './ObamaCards.scss';
import Slider from "react-slick";
import {useRef} from "react";
import {settings} from "../../../../components/Slider/sliderSetings";

export default function ObamaCards({cards}) {

    const SliderRef = useRef(null);

    const itemsList = cards.map(card => {
        return (
            <div className={'obama-cards__card'} key={card.title}>
                {card.background && <img src="/images/NftDevPage/Obama-opacity.png" alt="back"/>}

                <img className={'obama-cards__card-image'} src="/images/NftDevPage/Obama-circle.png" alt="Obama"/>
                <h3>{card.title}</h3>
                <p>{card.paragraph}</p>
            </div>
        );
    })

    return (
        <div className={'obama-cards'}>
            <div className="obama-cards__grid">
                {itemsList}
            </div>
            <div className="default__mobile-slider-wrap">
                <Slider ref={SliderRef} {...settings}>
                    {itemsList}
                </Slider>
            </div>
        </div>
    );
}