import './SmartTransaction.scss';
import SmartCard from "./SmartCards/SmartCard";
import {smartCards} from "../../../data/smartCards";
import {useSetRecoilState} from "recoil";
import {isModalOpenState} from "../../../components/layout/Modal/isModalOpenState";

export default function SmartTransaction() {

    const setModalIsOpen = useSetRecoilState(isModalOpenState);

    return (
        <section className="smart-transactions">
            <div className="container">
                <h2>Smart transactions begin here</h2>
                <div className="why-blockchain__flex">

                    {smartCards.map(card => {
                        return (
                            <SmartCard
                                card={card}
                                key={card.title}
                                onClick={() => setModalIsOpen(true)}
                            />
                        )
                    })}

                </div>
            </div>
        </section>
    );
}