import './MySlider.scss';
import {useRef} from "react";
import Slider from 'react-slick';
import {Link} from "react-router-dom";
import {ReactComponent as Arrow} from "../../icons/arrow_up-right.svg";
import {ReactComponent as NavArrow} from "../../icons/nav-arrow.svg";
import {sliderCards} from "./sliderCards";

export default function MySlider() {

    const SliderRef = useRef(null);
    const settings = {
        dots: true,
        infinite: true,
        arrows: false,
        speed: 500,
        slidesToShow: 1,
        swipeToSlide: true,
        initialSlide: 0,
        appendDots: dots => (
            <div className={'my-slider__nav-wrap'}>
                <ul>{dots}</ul>
            </div>
        ),
    };



    const sliderItems = sliderCards.map((card) => {

        const cardList = card.itemsList.map((item) => {
            return (
                <li key={item.number + item.desc}>
                    <div>
                        {item.number}
                    </div>
                    <div>
                        {item.desc}
                    </div>
                </li>
            )
        })

        return (
            <div className={'my-slider__slide'} key={card.title + card.href}>
                <div className="my-slider__inner flex">

                    <div className="my-slider__content-header_link my-slider__content-header_link_mobile flex">
                        <Link className={'flex'} to={card.href}>
                            {card.nameLink}
                            <Arrow/>
                        </Link>
                    </div>

                    <div className="my-slider__img-wrap flex">
                        <img src={`images/Slider/${card.image}`} alt="Slider"/>
                    </div>

                    <div className="my-slider__content">
                        <div className="my-slider__content-header flex">
                            <div className="my-slider__content-header_title">
                                {card.title}
                            </div>
                            <div className="my-slider__content-header_link flex">
                                <a href={card.href} target='_blank' rel='noreferrer' className={'flex'}>
                                    {card.nameLink}
                                    <Arrow/>
                                </a>
                            </div>
                        </div>
                        <div className="my-slider__content-text">
                            {card.text}
                        </div>
                        <div className="my-slider__content-list">
                            <ul>
                                {cardList}
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        );
    });

    return (
        <div className={'my-slider'}>
            <div className="my-slider_prev-arrow my-slider_nav-arrow" onClick={() => SliderRef.current.slickPrev()}>
                <NavArrow/>
            </div>
            <Slider ref={SliderRef} {...settings}>
                {sliderItems}
            </Slider>
            <div className="my-slider_next-arrow my-slider_nav-arrow" onClick={() => SliderRef.current.slickNext()}>
                <NavArrow/>
            </div>
        </div>
    );
}