import './HoverList.scss';
import {ReactComponent as ArrowRight} from "../../../icons/Arrow-hover.svg";

export default function HoverList({list}) {

    const items = list.map(item => {
        return (
            <li key={item.title}>
                <ArrowRight/>
                <div className="hover-list__content-wrap">
                    <h3>{item.title}</h3>
                    <p>{item.text}</p>
                </div>
            </li>
        )
    });

    return (
      <section className={'hover-list'}>
          <div className="container">
              <ul>
                  {items}
              </ul>
          </div>
      </section>
    );
}