import './Modal.scss'
import cn from "classnames";
import {useLocation} from "react-router-dom";
import {useEffect} from "react";

const stopScrollClass = 'stop-scroll';

export default function Modal({children, isOpened, onClose}) {

    const {pathname, hash} = useLocation();
    const scrollbarWidth = window.innerWidth - document.documentElement.clientWidth;

    useEffect(() => {
        onClose();
    }, [pathname, hash])

    useEffect(() => {
        if (isOpened) {
            document.body.classList.add(stopScrollClass);
            document.body.style.paddingRight = `${scrollbarWidth}px`;

        } else {
            setTimeout(() => {
                document.body.classList.remove(stopScrollClass);
                document.body.style.paddingRight = `0`;
            }, 200)
        }
    }, [isOpened])

    useEffect(() => {
        const onEscape = ({key}) => {
            if (key === 'Escape') {
                onClose()
            }
        }

        document.addEventListener('keydown', onEscape);
        return () => document.removeEventListener('keydown', onEscape);
    }, [])

    return (
        <div className={cn('modal flex', {'modal_opened': isOpened})}>
            <div className={'modal__inner flex'}>
                {children}
            </div>
        </div>
    );
}