import './WhatSemiFungibleToken.scss';
import SectionHeader from "../../../../components/SectionHeader/SectionHeader";
import BlockWithSquare from "../../../../components/innerPages/BlockWithSquare/BlockWithSquare";

export default function WhatSemiFungibleToken() {

    const semiFungibleTokenCards = [
        {
            image: '/images/innerPages/GameFi-cover.jpg',
            text: <>
                A semi-fungible token, or an SFT, merges advantageous characteristics of fungible and non-fungible
                tokens. That is, SFTs are fungible until they are used in some sort of an operation.<br/><br/>
                Semi-fungible tokens let artists, musicians, and other creative personalities express themselves in a
                much more cost-efficient way than with NFTs.
            </>
        },
        {
            image: '/images/innerPages/GameFi-cover.jpg',
            text: 'The semi-fungible coin technology accepts fungible and non-fungible tokens — one smart contract for any type of operation. In practice, this means that the gas fees are significantly reduced.'
        },
    ];

    return (
        <section className={'fungible-token'}>
            <div className="container">
                <SectionHeader
                    title={'What Is Semi Fungible Token?'}
                    subtitle={'Semi fungible tokens can be beneficial for both token holders and SFT creators.'}
                    marginBottom={'96'}
                />
                <div className="fungible-token__wrap">

                    {semiFungibleTokenCards.map((item, index) => {
                        return <BlockWithSquare content={item} reverse={index === 1}/>
                    })}

                </div>
            </div>
        </section>
    )
}