import './BlockWithIcon.scss';

export default function BlockWithIcon({item, number}) {


    return (
        <div className={'block-with-icon'}>
            <div className="block-with-icon__header-wrap">
                <div className="block-with-icon__img-wrap">
                    <img src={item.icon} alt="Icon"/>
                    {number && <div>{number}</div>}
                </div>
                {item.headerTitle && <h3>{item.headerTitle}</h3>}
            </div>
            {item.title && <h3>{item.title}</h3>}
            {item.text && <p>{item.text}</p>}
        </div>
    );
}