import './ScrollItems.scss';
import {gsap} from 'gsap';
import {ScrollTrigger} from 'gsap/ScrollTrigger';
import SectionHeader from "../../../components/SectionHeader/SectionHeader";
import {useEffect, useRef} from "react";

gsap.registerPlugin(ScrollTrigger);

export default function ScrollItems() {

    const itemsTrackRef = useRef();
    const itemsTrackSecondRef = useRef();

    const firstAnim = (elem) => gsap.to(elem, {
        x: () => 300,
        ease: "none",
        scrollTrigger: {
            trigger: elem,
            start: "top bottom",
            end: "bottom top",
            scrub: true,
            markers: false,
        }
    });
    const secondAnim = (elemSecond) => gsap.to(elemSecond, {
        x: () => -300,
        // x: () => -(elemSecond.clientWidth + window.innerWidth),
        ease: "none",
        scrollTrigger: {
            trigger: elemSecond,
            start: "top bottom",
            end: "bottom top",
            scrub: true,
        }
    });

    useEffect(() => {
        const elem = itemsTrackRef?.current;
        const elemSecond = itemsTrackSecondRef?.current;

        firstAnim(elem)
        secondAnim(elemSecond)
    }, [])

    const scrollingItems = [
        '#NFT Development',
        '#DeFi',
        '#Launch IDO',
        '#Blockchain',
        '#NFT collections',
        '#NFT Marketplace',
    ]
    const scrollingItemsSecond = [
        '#Launch IDO',
        '#Smart Contract',
        '#DeApp',
        '#GameFi',
        '#Smart Contract Audit',
        '#NFT Marketplace',
    ]

    const repeatArr = (arr, n) => [].concat(...Array(n).fill(arr));

    const itemsList = repeatArr(scrollingItems, 2).map((item, index) => {
        return (
            <div key={index} className="scroll-items__item">
                {item}
            </div>
        )
    })

    const itemsListSecond = repeatArr(scrollingItemsSecond, 2).map((item, index) => {
        return (
            <div key={index} className="scroll-items__item">
                {item}
            </div>
        )
    })

    return (
        <section className={'scroll-items'}>
            <div className="container">
                <SectionHeader title={'What kind of projects we work with'} marginBottom={'43'}/>
            </div>
            <div className="scroll-items__list-wrap">
                <div ref={itemsTrackRef} className="scroll-items__track">
                    {itemsList}
                </div>
                <div ref={itemsTrackSecondRef} className="scroll-items__track">
                    {itemsListSecond}
                </div>
            </div>
        </section>
    )
}