import './Header.scss';
import {Link, useLocation} from "react-router-dom";
import cn from 'classnames';
import {ReactComponent as ArrowDown} from "../../../icons/Arrow-down.svg";
import {ReactComponent as ArrowRight} from "../../../icons/Arrov-right.svg";
import LoginButton from "./LoginButton/LoginButton";
import {navigation, navigationFull} from "../../../data/navMenu";
import {isShowMenuState} from "../../../states/isShowMenuState";
import {useRecoilState} from "recoil";
import {useEffect, useRef, useState} from "react";
import {isModalOpenState} from "../Modal/isModalOpenState";
import {emails} from "../../../data/emails";

export default function Header() {

    const {pathname} = useLocation();
    const headerRef = useRef(null);
    const dropDownRef = useRef(null);
    const [isShowMobileMenu, setIsShowMobileMenu] = useRecoilState(isShowMenuState);
    const [isDropDownOpen, setIsDropDownOpen] = useState(false);
    const [isMobileSecondDropDownOpen, setIsMobileSecondDropDownOpen] = useState(false);
    const [isActiveBurgerAnimation, setIsActiveBurgerAnimation] = useState(false);

    const [modalIsOpen, setModalIsOpen] = useRecoilState(isModalOpenState);
    const scrollbarWidth = window.innerWidth - document.documentElement.clientWidth;

    useEffect(() => {
        if (modalIsOpen) {
            headerRef.current.style.paddingRight = `${scrollbarWidth}px`
        } else {
            setTimeout(() => {
                headerRef.current.style.paddingRight = `0`
            }, 200)
        }
    }, [modalIsOpen])

    useEffect(() => {
        document.addEventListener('click', (event) => {
            setIsDropDownOpen(dropDownRef.current.contains(event.target))
        });
    }, [])

    useEffect(() => {
        setIsDropDownOpen(false);
        setIsShowMobileMenu(false);
    }, [pathname])

    const onNavMenuClick = () => setIsDropDownOpen(!isDropDownOpen)

    const onBurgerClick = (e) => {
        e.stopPropagation();
        if (!isActiveBurgerAnimation) {
            setIsActiveBurgerAnimation(true);
        }
        if (isMobileSecondDropDownOpen) {
            setIsMobileSecondDropDownOpen(!isMobileSecondDropDownOpen)
        } else {
            setIsShowMobileMenu(!isShowMobileMenu)
        }
    }

    const onMobileOverlayClick = () => {
        if (isMobileSecondDropDownOpen) {
            setIsMobileSecondDropDownOpen(!isMobileSecondDropDownOpen)
        }
    }

    return (
        <header ref={headerRef}>
            <div className="container">
                <Link className="logo-wrap" to="/">
                    <img src="/images/logos/itWorkin-logo-main.svg" alt="Logo" className="logo"/>
                </Link>
                <nav className="navigation">
                    <ul className="navigation-list">
                        <li
                            className={cn("navigation-list__link navigation-list__link_first", {'navigation-list__link_first_active': isDropDownOpen})}
                        >
                            <div
                                className={'navigation-list__link'}
                                onClick={onNavMenuClick}
                                ref={dropDownRef}
                            >
                                Services
                                <ArrowDown/>
                            </div>
                            <div
                                className={cn("navigation-list__drop-down", {'navigation-list__drop-down_visible': isDropDownOpen})}>
                                <ul className="navigation-list__drop-down-list">

                                    {navigationFull.map(item => {
                                        return (
                                            <li key={item.name} className="navigation-list__drop-down-item">
                                                <Link to={item.link}>{item.name}</Link>
                                            </li>
                                        )
                                    })}

                                </ul>
                            </div>
                        </li>
                        {navigation.slice(1).map(elem => {
                            return (
                                <li
                                    className={cn('navigation-list__link', {'navigation-list__link_ghost': elem.ghost})}
                                    key={elem.name}
                                >
                                    <Link to={elem.link}>{elem.name}</Link>
                                </li>
                            );
                        })}
                    </ul>
                </nav>
                <LoginButton onClick={() => setModalIsOpen(true)}/>
                <div
                    className={cn("hamburger", {'hamburger_anim': isActiveBurgerAnimation, 'hamburger_active': isShowMobileMenu})}
                    onClick={onBurgerClick}
                >
                    <div className="hamburger__line"/>
                    <div className="hamburger__line"/>
                    <div className="hamburger__line"/>
                </div>
            </div>

            <div
                onClick={onMobileOverlayClick}
                className={cn('mobile-menu', {'show-menu': isShowMobileMenu})}
            >
                <div className="mobile-menu__inner">
                    <nav>
                        <ul className="mobile-menu__navigation-list_mobile mobile-navigation-list">

                            {navigation.map((elem, index) => {
                                if (index === 0) {
                                    return (
                                        <li
                                            key={elem.name}
                                            onClick={() => setIsMobileSecondDropDownOpen(true)}
                                            className={'mobile-navigation-list_item mobile-navigation-list_item_opening'}
                                        >
                                            <div>{elem.name}</div>
                                            <ArrowRight/>

                                            <div className={cn('second-drop-down-wrapper', {'second-drop-down-wrapper_opened': isMobileSecondDropDownOpen})}>
                                                <ul>
                                                    {navigationFull.map(item => {
                                                        return (
                                                            <li key={item.secondName}>
                                                                <Link to={item.link}>
                                                                    {item.secondName}
                                                                </Link>
                                                            </li>
                                                        )
                                                    })}

                                                </ul>
                                            </div>
                                        </li>
                                    )
                                } else {
                                    return (
                                        <li
                                            className={cn('mobile-navigation-list_item', {'navigation-list__link_ghost': elem.ghost})}
                                            key={elem.name}>
                                            <Link to={elem.link}>{elem.name}</Link>
                                        </li>
                                    );
                                }
                            })}

                        </ul>
                    </nav>
                    <div className="group-mail">
                        <div>
                            <a href={`mailto:${emails.info}`}>{emails.info}</a>
                            <span>
                                <img className="arrow" src="/images/arrow.png" alt="Arrow"/>
                            </span>
                        </div>
                        <div>
                            <a href={`mailto:${emails.marketing}`}>{emails.marketing}</a>
                            <span>
                                <img className="arrow" src="/images/arrow.png" alt="Arrow"/>
                            </span>
                        </div>
                        <button
                            onClick={() => setModalIsOpen(true)}
                            className="order-button-border"
                        >
                            ORDER A PROJECT
                        </button>
                    </div>
                </div>
            </div>
        </header>
    )
};