import './Author.scss';

export default function Author({author, dark}) {
    return (
        <div className="author flex">
            <img src={`/images/MainDevBlog/${author.gender}`} alt="Gender" className="author__icon"/>
            <div style={dark ? {color: '#0D0D0D'} : {color: '#F6F9F6'}} className="author__name">
                {author.name}
                <div className="author__post">
                    {author.post}
                </div>
            </div>
        </div>
    );
}