import './StepCard.scss';

export default function StepCard({card, index}) {
    return (
        <div className={'step-card'}>
            <img src={card.image} alt="Card Icon"/>

            {index && <div>Step {index}</div>}

            <h3>{card.title}</h3>
            <p>{card.text}</p>
        </div>
    );
}